  40% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
@-webkit-keyframes page-loader-outer {
  0% {
    transform: scale(1);
  }
  40%,
  100% {
    transform: scale(0);
    opacity: 0;
  }
}
@keyframes page-loader-outer {
